@font-face {
	font-family:'noto';
	font-weight:300;
	src:local('NotoSansLight'), local('NotoSansLight'),
		url(../font/noto/NotoSansKR-Light.eot);
	src:url(../font/noto/NotoSansKR-Light.eot?#iefix) format('embedded-opentype'),
		url(../font/noto/NotoSansKR-Light.woff2) format('woff2'),
		url(../font/noto/NotoSansKR-Light.woff) format('woff');
}
